* {
  font-family: "Inter UI", -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif;
}

div {
  background-color: white;
}

a {
  color: #3463d9;
  text-decoration: underline;
}

.event-table {
  text-align: left;
  display: inline-block;
  text-align: left;

}

.signup-table {
  display: inline-block;
}

td {
  padding-right: 10px;
}

th {
  color: #3463d9;
}

form {
  -ms-flex-pack: center;
      justify-content: center;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

h1 {
  margin-left: auto;
  margin-right: auto;
}

h1, h2 {
  padding: 1rem;
  font-family: "Inter UI", -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif;
  font-weight: 200;
  text-align: center;
}

h3 {
  font-weight: 600;
  font-size: 2rem;
  color: #3463d9;
}

h4, h5, h6 {
  padding: 0.5rem;
  font-family: "Inter UI", -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif;
  font-weight: 200;
  text-align: center;
}

hr {
  background-color: #3463d9;
}

input {
  margin: 0.5rem;
}

p {
  padding-top: 0.5rem;
  padding-left: 4rem;
  padding-right: 4rem;
  padding-bottom: 0.5rem;
}

ol {
  padding-top: 0.5rem;
  padding-left: 4rem;
  padding-right: 4rem;
  padding-bottom: 0.5rem;
  line-height: 1.5rem;
}

thead {
    background-color: white;
}

.App {
  /**
  background-image: url('./scaleth.svg');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: bottom;
  position: sticky;
  **/
  max-width: 100vw;
  min-height: 100vh;
  padding: 0px;
  margin: 0px;
}

.donation-button {
  margin: 0.5rem;
  padding: 0.5rem 1.5rem;
  min-width: 10vw;
  height: 2.5rem;
  -ms-flex-item-align: center;
      align-self: center;
  -webkit-box-shadow: 0px 2px 4px rgba(43, 59, 71, 0.124066);
          box-shadow: 0px 2px 4px rgba(43, 59, 71, 0.124066);
  border-radius: 20px;
  background-color: #000;
  color: #fff;
  font-size: 16px;
}

.donation-button:hover {
  color: #000;
  background-color: #fff;
}

.container-fluid {
  background-color: white;
  padding: 0px;
  margin: 0px;
  max-width: 99vw;
}

.flex-column {
  margin: 2rem, 2.5rem, 2rem, 2.5rem;
}

.row, .flex-row {
  max-width: 99vw;
  padding-bottom: 1rem;
}

.Entry {
  background-color: white;
}

.Search {
  padding: 0px;
  margin: 0px;
}

.typelogo {
  padding: 2rem 2rem 2rem 2rem;
  max-height: 33vh;
}

.amount {
  display: flex-wrap;
  -ms-flex-pack: center;
      justify-content: center;
}

.margin {
  margin: 1rem;
}

.donationColumn {
  background: white;
  background-color: white;
  color: #000;
  -ms-flex-pack: center;
      justify-content: center;
  text-align: center;
  /* margin-top: 2rem; */
  padding-left: 1rem;
  padding-right: 1rem;
  /* padding-bottom: 0.5rem; */
}

.introContainer {
  padding-bottom: 5rem;
}
 
img {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.leaderboard {
  overflow-x: auto;
}

.table-tx-header {
  max-width: 15vw;
}

.qr-code {
  width: 15vw;
  height: auto;
  padding: 0.5rem;
}

.word-wrap {
  font-size: 0.9rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  font-weight: 500;
  color: #000;
  -o-text-overflow: clip;
     text-overflow: clip;
  background: white;
}

.donation {
  background: white;
}

.donation-button {
  background: #000;
}

.clear {
  clear: both;
}

.token-select {
  margin-top: 8px;
  height: 46px;
}



